import {DataState, DataReducer, SET_DATA, SET_DATA_ERROR, SET_DATA_SET} from "./types";

const initialState: DataState = {
  dataset: null,
  data: null,
  error: null,
  loading: false
}

export const dataReducer: DataReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_DATA_SET:
      return { ...state, loading: true, error: null, dataset: action.payload }
    case SET_DATA:
      return { ...state, data: action.payload, loading: false }
    case SET_DATA_ERROR:
      return { ...state, loading:false, error: action.payload  }
    default:
      return state
  }
}
