import { put, takeLatest } from 'redux-saga/effects'
import { Environment } from '../../environment';
import axios from 'axios'

import {LOGIN_USER, LoginAction} from "./types";
import {setAuthErrorAction, setUserAuthStatus} from "./actions";
import {store} from "../../util/browserStorage";
import {StorageKeys} from "../../consts/StorageKeys";

const AUTH_ENDPOINT = 'login'

function* loginUser(action: LoginAction) {
  try {
    const user = yield axios.post(`${Environment.ApiUrl}/${AUTH_ENDPOINT}`, action.payload)
    store(StorageKeys.Token, user.data.token)
    yield put(setUserAuthStatus(!!user.data.token));
  } catch (e) {
    yield put(setAuthErrorAction(e.message));
  }
}

export function* loginUserSaga() {
  yield takeLatest(LOGIN_USER, loginUser);
}
