import {SET_DATA, SET_DATA_ERROR, SET_DATA_SET, SetDataAction, SetDataErrorAction, SetDatasetAction,} from "./types";
import {DataSetName} from "../../consts/DataSetName";

export const setDatasetAction: (dataset: DataSetName|null) => SetDatasetAction = (dataset) => ({
  type: SET_DATA_SET,
  payload: dataset
})

export const setDataAction: (data: unknown) => SetDataAction = data => ({
  type: SET_DATA,
  payload: data
})

export const setDataErrorAction: (error?: string | null ) => SetDataErrorAction = error => ({
  type: SET_DATA_ERROR,
  payload: error || null
})
