import {DataSetName} from "../../consts/DataSetName";

export const SET_DATA_SET = 'SET_DATA_SET'
export const SET_DATA = 'SET_DATA'
export const SET_DATA_ERROR = 'SET_DATA_ERROR'

export type DataState<T = unknown> = {
  dataset: DataSetName | null;
  data: T;
  error: string | null;
  loading: boolean
}


export type SetDatasetAction = {
  type: typeof SET_DATA_SET,
  payload: DataSetName | null
}


export type SetDataErrorAction = {
  type: typeof SET_DATA_ERROR,
  payload: string | null
}

export type SetDataAction = {
  type: typeof SET_DATA;
  payload: unknown
}

export type KPIData = {
  title: string
  value: string
}

export type QualityData = {
  adQuality: number;
  filteredByAttribute: { attribute: string; unwantedRatio: number }[];
  filteredByDemandSource: { demandSource: string; unwantedRatio: number }[]
  unwantedAds: number
}

export type ChartData = {
  dataId: number;
  dataset: {
    controlData: number[];
    data: number[]
  },
  labels: string[];
  title: string
}

export type DataAction = SetDatasetAction | SetDataErrorAction | SetDataAction

export type DataReducer = (state: DataState, action: DataAction) => DataState
