import {
  LOGIN_USER,
  LoginAction,
  SET_AUTH_ERROR,
  SET_USER_AUTH_STATUS,
  SetAuthErrorAction,
  SetUserAuthStatusAction
} from "./types";

export const loginUserAction: (user: string, password: string) => LoginAction = (user, password) => ({
  type: LOGIN_USER,
  payload: {
    User: user,
    Password: password
  }
})

export const setUserAuthStatus: (status: boolean) => SetUserAuthStatusAction = status => ({
  type: SET_USER_AUTH_STATUS,
  payload: status
})

export const setAuthErrorAction: (error?: string | null ) => SetAuthErrorAction = error => ({
  type: SET_AUTH_ERROR,
  payload: error || null
})
