export const LOGIN_USER = 'LOGIN_USER'
export const SET_USER_AUTH_STATUS = 'SET_USER_AUTH_STATUS'
export const SET_AUTH_ERROR = 'SET_AUTH_ERROR'

export type AuthState = {
  authorized: boolean;
  error: string | null;
  loading: boolean
}

export type LoginActionPayload = {
  User: string;
  Password: string;
}


export type LoginAction = {
  type: typeof LOGIN_USER,
  payload: LoginActionPayload
}


export type SetAuthErrorAction = {
  type: typeof SET_AUTH_ERROR,
  payload: string | null
}

export type SetUserAuthStatusAction = {
  type: typeof SET_USER_AUTH_STATUS;
  payload: boolean
}

export type AuthAction = SetUserAuthStatusAction | LoginAction | SetAuthErrorAction

export type AuthReducer = (state: AuthState, action: AuthAction) => AuthState
