import {put, takeLatest} from 'redux-saga/effects'
import {Environment} from '../../environment';
import axios from 'axios'

import {SET_DATA_SET, SetDatasetAction} from "./types";
import {setDataAction, setDataErrorAction} from "./actions";
import {get} from "../../util/browserStorage";
import {StorageKeys} from "../../consts/StorageKeys";
import {DataSetName} from "../../consts/DataSetName";

function* getDatasetData(action: SetDatasetAction) {
  if (!action.payload) {
    return yield put(setDataAction(null))
  }

  if (action.payload === DataSetName.Pies) {
    yield put(setDataAction([]))
    return
  }

  const offset = (24*60*60*1000) * 7

  try {
    const res = yield axios.get(
      `${Environment.ApiUrl}/${action.payload}?dateFrom=${Date.now() - offset}&dateTo=${Date.now()}&site_id=1`,
      { headers: {'Authorization': `Bearer ${get(StorageKeys.Token)}` } }
      )
    yield put(setDataAction(res.data));
  } catch (e) {
    yield put(setDataErrorAction(e.message));
  }
}

export function* dataSaga() {
  yield takeLatest(SET_DATA_SET, getDatasetData);
}
