import {AuthReducer, AuthState, LOGIN_USER, SET_AUTH_ERROR, SET_USER_AUTH_STATUS} from "./types";
import {get} from "../../util/browserStorage";
import {StorageKeys} from "../../consts/StorageKeys";

console.log(!!get(StorageKeys.Token), get(StorageKeys.Token))
const initialState: AuthState = {
  authorized: !!get(StorageKeys.Token),
  error: null,
  loading: false
}

export const authReducer: AuthReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_USER:
      return { ...state, loading: true, error: null }
    case SET_USER_AUTH_STATUS:
      return { ...state, authorized: action.payload, loading: false }
    case SET_AUTH_ERROR:
      return { ...state, loading:false, error: action.payload  }
    default:
      return state
  }
}
